import * as d3 from "d3";

import data from "./day.csv";
function test2() {

    // 设置SVG的宽度和高度
    var width = 1000;
    var height = 500;
    var margin = { top: 40, right: 50, bottom: 60, left: 50 };
    var innerWidth = width - margin.left - margin.right;
    var innerHeight = height - margin.top - margin.bottom;

    // 创建一个div元素作为SVG容器
    var container = d3.select("body")
        .append("div")
        .attr("class", "chart-container");

    // 添加标题
    container.append("h2")
        .text("南开大学津南校区10月的温度曲线图");

    // 创建SVG
    var svg = container.append("svg")
        .attr("width", width)
        .attr("height", height);

    // 创建一个内部容器，并添加到SVG中，作为绘制图形的区域
    var g = svg.append("g")
        .attr("transform", "translate(" + margin.left + "," + margin.top + ")");

    // 创建一个比例尺，将日期映射到X轴上的位置
    var xScale = d3.scaleBand()
        .domain(data.map(d => d.day))
        .range([0, innerWidth])
        .padding(0.1);

    // 创建一个比例尺，将温度映射到Y轴上的位置
    var yScale = d3.scaleLinear()
        .domain([0, d3.max(data, d => Math.max(d.high, d.low))])
        .range([innerHeight, 0]);

    // 创建一个X轴和Y轴生成器，用于在SVG中添加这些轴
    var xAxis = d3.axisBottom(xScale);
    var yAxis = d3.axisLeft(yScale);

    // 在SVG中添加X轴
    g.append("g")
        .attr("class", "x-axis")
        .attr("transform", "translate(0," + innerHeight + ")")
        .call(xAxis);
    // 在SVG中添加Y轴
    g.append("g")
        .attr("class", "y-axis")
        .call(yAxis);

    // 创建最高温度曲线
    var line1 = d3.line()
        .x(d => xScale(d.day) + xScale.bandwidth() / 2)
        .y(d => yScale(d.high))
        .curve(d3.curveBumpX);
    // 创建最低温度曲线
    var line2 = d3.line()
        .x(d => xScale(d.day) + xScale.bandwidth() / 2)
        .y(d => yScale(d.low))
        .curve(d3.curveBumpX);

    // 在SVG中绘制最高温度曲线
    g.append("path")
        .datum(data)
        .attr("fill", "none")
        .attr("stroke", "red")
        .attr("stroke-width", 2.5)
        .attr("d", line1)
        .style("stroke-dasharray", ("3, 3")) // 添加虚线效果
        .style("stroke-linejoin", "round") // 设置线段连接处为圆角
        .style("stroke-linecap", "round") // 设置线段末端为圆角
        .transition()
        .duration(1000)
        .attrTween("stroke-dasharray", function () {
            var length = this.getTotalLength();
            return function (t) {
                return d3.interpolateString("0," + length, length + "," + length)(t);
            };
        });

    // 在SVG中绘制最低温度曲线
    g.append("path")
        .datum(data)
        .attr("fill", "none")
        .attr("stroke", "blue")
        .attr("stroke-width", 2.5)
        .attr("d", line2)
        .style("stroke-dasharray", ("3, 3")) // 添加虚线效果
        .style("stroke-linejoin", "round") // 设置线段连接处为圆角
        .style("stroke-linecap", "round") // 设置线段末端为圆角
        .transition()
        .duration(1000)
        .attrTween("stroke-dasharray", function () {
            var length = this.getTotalLength();
            return function (t) {
                return d3.interpolateString("0," + length, length + "," + length)(t);
            };
        });

    // 绘制最高温度数据点
    g.selectAll(".data-point-high")
        .data(data)
        .enter()
        .append("circle")
        .attr("class", "data-point-high")
        .attr("cx", d => xScale(d.day) + xScale.bandwidth() / 2)
        .attr("cy", d => yScale(d.high))
        .attr("r", 0)
        .style("fill", "red") // 设置填充颜色为红色
        .on("mouseover", function () {
            d3.select(this)
                .transition()
                .attr("r", 6);
        })
        .on("mouseout", function () {
            d3.select(this)
                .transition()
                .attr("r", 4);
        })
        .transition()
        .delay(1000)
        .duration(500)
        .attr("r", 4);

    // 绘制最低温度数据点
    g.selectAll(".data-point-low")
        .data(data)
        .enter()
        .append("circle")
        .attr("class", "data-point-low")
        .attr("cx", d => xScale(d.day) + xScale.bandwidth() / 2)
        .attr("cy", d => yScale(d.low))
        .attr("r", 0)
        .style("fill", "blue") // 设置填充颜色为蓝色
        .on("mouseover", function () {
            d3.select(this)
                .transition()
                .attr("r", 6);
        })
        .on("mouseout", function () {
            d3.select(this)
                .transition()
                .attr("r", 4);
        })
        .transition()
        .delay(1000)
        .duration(500)
        .attr("r", 4);

    // 添加最高温度标签
    g.selectAll(".data-label-high")
        .data(data)
        .enter()
        .append("text")
        .attr("class", "data-label-high")
        .attr("x", d => xScale(d.day) + xScale.bandwidth() / 2)
        .attr("y", d => yScale(d.high) - 10)
        .text(d => d.high)
        .style("text-anchor", "middle")
        .style("opacity", 0)
        .transition()
        .delay(1000)
        .duration(500)
        .style("opacity", 1);

    // 添加最低温度标签
    g.selectAll(".data-label-low")
        .data(data)
        .enter()
        .append("text")
        .attr("class", "data-label-low")
        .attr("x", d => xScale(d.day) + xScale.bandwidth() / 2)
        .attr("y", d => yScale(d.low) + 15)
        .text(d => d.low)
        .style("text-anchor", "middle")
        .style("opacity", 0)
        .transition()
        .delay(1000)
        .duration(500)
        .style("opacity", 1);

    // 添加数据点和标签的鼠标交互
    g.selectAll(".data-point-high, .data-point-low")
        .on("mouseover", function () {
            d3.select(this)
                .transition()
                .attr("r", 6);
        })
        .on("mouseout", function () {
            d3.select(this)
                .transition()
                .attr("r", 4);
        });

}

export default test2;
test2();